import { __awaiter } from "tslib";
import core from '@/core';
import { beautify } from '@/requests';
import { alas, status, action } from '@/alas';
import { Self } from '@/vue-core';
import { defineComponent, reactive, onUnmounted, ref } from '@vue/composition-api';
export default defineComponent({
    props: {},
    setup(props, context) {
        let self = new Self(context);
        // =================
        //
        // ref
        //
        let list = ref(null);
        let create = ref(null);
        // =================
        //
        // state
        //
        let $ = reactive({
            alas,
            project: status.fetch('project'),
            target: null,
            compile: null,
            createName: ''
        });
        // =================
        //
        // mounted
        //
        onUnmounted(() => {
            core.reloadCustom($.project);
        });
        // =================
        //
        // methods
        //
        let getBtnScript = () => __awaiter(this, void 0, void 0, function* () {
            let script = `
                {
                    info: '等待',
                    help: '詳細說明',
                    color: '#000000',
                    btnText: '等待',
                    props: {
                        value: {
                            type: 'text',
                            info: '毫秒',
                            default: '1000'
                        }
                    },
                    display(props) {
                        return '等待' + props.value + '毫秒'
                    },
                    validate(props) {
                        let result = Number(props.value)
                        if (isNaN(result)) {
                            return '必須為數字'
                        } else {
                            return true
                        }
                    },
                    write(props) {
                        return 'await browser.sleep(' + props.value + ')'
                    }
                }
            `;
            let result = yield beautify(script, 8);
            return result;
        });
        let select = (btn) => {
            $.target = btn;
            self.forceUpdate();
        };
        let createBtn = () => {
            create.value.open(() => __awaiter(this, void 0, void 0, function* () {
                if ($.project.customBtns.fetch($.createName)) {
                    action.message('error', '相同名稱按鈕已經存在');
                }
                else {
                    $.project.customBtns.write({
                        name: $.createName,
                        script: yield getBtnScript()
                    });
                    $.target = $.project.customBtns.fetch($.createName);
                    self.forceUpdate();
                    self.nextTick(() => {
                        list.value.$el.scrollTo(0, 999999999);
                    });
                }
                $.createName = '';
            }));
        };
        let verifyContent = () => __awaiter(this, void 0, void 0, function* () {
            if ($.target == null) {
                $.compile = null;
            }
            let result = null;
            try {
                eval('result = ' + $.target.script);
            }
            catch (error) {
                return $.compile = `編譯失敗(${error.message})`;
            }
            try {
                core.verifyTemplate(Object.assign({ name: 'test', type: 'action' }, result));
                $.compile = null;
            }
            catch (error) {
                $.compile = `格式不正確(${error.message})`;
            }
        });
        // =================
        //
        // done
        //
        return {
            $,
            list,
            create,
            getBtnScript,
            select,
            createBtn,
            verifyContent
        };
    }
});
